import { bindable, customElement, containerless } from 'aurelia-framework';
import * as kramed from 'kramed';

@containerless()
@customElement('x-container')
export class Container {
  @bindable() pageTitle;
  @bindable() source = '';
  @bindable() wiki = '';

  currentView = 0;

  elSource: any = {};
  elWiki: any = {};
  attached() {
    this.elSource.innerHTML = this.renderHtml(this.source);
    this.elWiki.innerHTML = this.renderHtml(this.wiki);
  }

  sourceChanged(newValue) {
    this.elSource.innerHTML = this.renderHtml(newValue);
  }

  wikiChanged(newValue) {
    this.elWiki.innerHTML = this.renderHtml(newValue);
  }

  renderHtml(html) {
    let rend = new kramed.Renderer();
    rend.code = (code, lang) => {
      code = code.replace(/&gt;/g, '>').replace(/&lt;/g, '<').replace(/&amp;/g, '&');
      if (window.hljs) {
        window.hljs.configure({
          useBR: true,
          tabReplace: '    '
        });
        return `<pre><code class="hljs ${lang} lang-${lang}">` + window.hljs.highlightAuto(code, [lang]).value + '</code></pre>';
      }
      else {
        return `<pre><code class="hljs ${lang} lang-${lang}"><xmp>${code}</xmp></code></pre>`;
      }
    }
    return kramed(html, {
      gfm: true,
      tables: true,
      breaks: true,
      pedantic: false,
      sanitize: false,
      smartLists: true,
      smartypants: false,
      renderer: rend
    });
  }
}
